import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    marginLeft: '5em',
    
    
    
    '& > *': {
      margin: theme.spacing(1),
      width: '100vh',
      display: 'flex',
      
    },

  },

  titulo: {
    fontSize: '2.5em'
  },

  botonEnviar: {
    height:'3em',
    fontSize: '2em'
  }
}));

export default function BasicTextFields() {
  const classes = useStyles();

  return (
      
    <Grid container >
      <Grid item xs={1} sm={6} md={4} >
        <form className={classes.root} noValidate autoComplete="off">
        <h2 className={classes.titulo}>Formulario de contacto</h2>
      <TextField id="outlined-basic" label="Nombre" variant="outlined" />
      <TextField id="outlined-basic" label="Apellido" variant="outlined" />
      <TextField id="outlined-basic" label="Correo" variant="outlined" />
      <TextField
          id="outlined-basic"
          label="Escriba su comentario..."
          multiline
          rows={4}
          variant="outlined"
        />
      <Button variant="contained" color="secondary" size="large" className={classes.botonEnviar} >
        ENVIAR
      </Button>

    </form>
      </Grid>
    </Grid>
    
  );
}