import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import theme from '../theme/theme';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { Link, Redirect } from 'react-router-dom';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import productos from '../component/productos';
import Typography from '@material-ui/core/Typography';

import Container from '@material-ui/core/Container';





const useStyles = makeStyles({
    contenedor: {
        display: 'flex',
        marginLeft:theme.spacing(2)
        
    },
    cardGrid: {
        paddingTop: theme.spacing(5),
        paddingBottom: theme.spacing(5),
      },
      card: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        borderRadius: '0',
        cursor: 'pointer',
      },
      cardMedia: {
        paddingTop: '90.25%', // 16:9
      },
      cardContent: {
        flexGrow: 1,
        
      }
    
  });

  

const Tienda = () => {

  

    
    const classes = useStyles();

    return (
        <Container className={classes.cardGrid} maxWidth="md"  >
          {/* End hero unit */}
          <Typography variant="h2" color="initial" align="center" style={{paddingBottom: '1em'}}>
            BORDADOS
          </Typography>
          <Grid container spacing={4}>
            {productos.map((prod, index) => (
              <Grid item  xs={12} sm={6} md={4}>
                <Link to={productos[index].url}>
                  <Card className={classes.card} >
                  <CardMedia
                    className={classes.cardMedia}
                    image={productos[index].imagen}
                    title={productos[index].producto}
                  />
                  <CardContent className={classes.cardContent}>
                    <Typography gutterBottom variant="h5" component="h2" align='center'>
                    {productos[index].producto}
                    </Typography>
                    <Typography align='center' variant="h5" color="">
                    ${productos[index].precio}
                    </Typography>
                  </CardContent>
                  
                </Card>
                </Link>  
              </Grid>
            ))}
          </Grid>
        </Container>
    );
};

export default Tienda;