import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';

import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography'






const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  boton: {
    color: 'white',
    padding: "0 0.5em",
    fontSize: '1.2em',
    margin: '0 0.6em', 
    "&:hover": {
      backgroundColor: '#C25776'
    }
  },
  linkMenu: {
    color:'white',
    textDecoration: 'none',
    fontSize: '2em',
    
  },
  login: {
    
    padding: '0.1em 1em',
    fontSize: '1.1em',
    backgroundColor: '#574555'
  },
  barra: {
    height: '5em',
    
  }

  
  
}));



export default function ButtonAppBar({Link}) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static">
        
        <Toolbar className={classes.barra}>
          <Grid container  justify='center' direction='row'>
          
          <Typography  color="initial">
          <Link className={classes.linkMenu} to="/plusultra-tienda"><Button variant="text" color="default" className={classes.boton}>HOME</Button></Link>
          <Link className={classes.linkMenu} to="/tienda"><Button variant="text" color="default" className={classes.boton}>TIENDA</Button></Link>
          <Link className={classes.linkMenu} to="/contacto"><Button variant="text" color="default" className={classes.boton}>CONTACTO</Button></Link>
          <Link className={classes.linkMenu} to="/micuenta"><Button variant="text" color="default" className={classes.boton}>MI CUENTA</Button></Link>
          </Typography>
          
          

          </Grid>

          <Button className={classes.login} variant="text"><Link className={classes.linkMenu} to="/login">Login</Link></Button>
        </Toolbar>
      </AppBar>
    </div>
  );
}