import { createMuiTheme } from '@material-ui/core/styles';
import {pink} from '@material-ui/core/colors';

const theme = createMuiTheme({
        palette: {
            primary: {
                main: pink[700],
            },
            secondary: {
                main: pink[300],
            }
        },
        typography: {
            fontFamily: [
                'Bebas Neue',
                'Montserrat'
            ].join(','),
          },
        
      
})

export default theme;