import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import Container from '@material-ui/core/Container'
import productos from '../component/productos';
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'






const useStyles = makeStyles({
    root: {
        

    },
    card: {
        height: '100%',
        display: 'flex',
        flexWrap:'wrap',
        borderRadius: '0',
      },
      cardMedia: {
        height:'28em'
      },
      contenedorProducto: {
          display: 'flex',
          width: '100%',
          padding: '2em 4.5em'
          
      },
      descripcion: {
          padding:'2em 0 0 2em ',
          width: '100%',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'space-around'
      },
      separador: {
          borderBottom: 'solid 1px #e0dede',
          borderTop: 'solid 1px #e0dede',
          paddingTop: '0.2em'
          
      },
      contDesc: {
        fontFamily: 'Montserrat'
      }
      

    
  });

const BordadoGoku = () => {

    const classes = useStyles();

    return (
        <div className={classes.root} Link={Link}>
            
                <Container maxWidth="xl" style={{margin:0}}>

                    <Grid item className={classes.contenedorProducto}>
                        <div className={classes.contenedorImg}>
                            <Card className={classes.card}>
                                {/* <CardMedia
                                className={classes.cardMedia}
                                image={productos[0].imagen}
                                title={productos[0].producto}
                                />
                              
                              <CardActions>
                                <Button size="small"></Button>
                              </CardActions> */}
                              <img src={productos[2].imagen} alt="" srcset="" className={classes.cardMedia}/>
                            </Card>

                        </div>
                        <div className={classes.descripcion}>
                            
                              
                            
                            <Typography variant="h3" color="initial">
                                {productos[2].producto}
                            </Typography>
                            
                            <Typography variant="h6" color="initial" className={classes.contDesc}>
                                {productos[2].descripcion}
                            </Typography>
                            <div className={classes.separador}></div>
                            <div className={classes.precio}>
                                <Typography variant="h4" color="secondary">
                                ${productos[2].precio}
                                </Typography>
                            </div>
                            <div className={classes.boton}>
                                <Button variant="outlined" color="secondary" size="large">
                                    AGREGAR AL CARRITO
                                </Button>
                            </div>
                            

                        </div>
                      
                    </Grid>
                  
                </Container>
            
            
        </div>
    );
};

export default BordadoGoku;