import React from 'react';
import theme from './theme/theme'
import {ThemeProvider} from '@material-ui/core'
import AppBar from './component/AppBar'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Contacto from './component/Contacto';
import Tienda from './component/Tienda';
import Home from './component/Home';
import MiCuenta from './component/MiCuenta';
import PrivateRoute from './component/PrivateRoute';
import {AuthProvider} from './component/Auth'
import Registro from './component/Registro';
import Login from './component/Login';
import BordadoAll from './contProductos/BordadoAll';
import BordadoAsuka from './contProductos/BordadoAsuka';
import BordadoBanana from './contProductos/BordadoBanana';
import BordadoGoku from './contProductos/BordadoGoku';
import BordadoHisoka from './contProductos/BordadoHisoka';
import BordadoNaruto from './contProductos/BordadoNaruto';
import BordadoTodoroki from './contProductos/BordadoTodoroki';
import BordadoHitachi from './contProductos/BordadoHitachi';
import Footer from './component/Footer';




function App() {
  return (
    <AuthProvider>
    <Router>

    <ThemeProvider theme={theme}>
    <div className="App">
      <AppBar Link={Link}/>       

        <Switch>
          
          <Route exact path="/plusultra-tienda" component={Home} />

          <Route exact path="/contacto" component={Contacto} />
          
          <Route exact path="/tienda" component={Tienda} />
          
          <Route exact path="/registro" component={Registro} />

          <Route exact path="/login" component={Login} />

          <Route exact path="/plusultra-tienda/bordado-allmight" component={BordadoAll} />
          <Route exact path="/plusultra-tienda/bordado-asuka" component={BordadoAsuka} />
          <Route exact path="/plusultra-tienda/bordado-banana" component={BordadoBanana} />
          <Route exact path="/plusultra-tienda/bordado-goku" component={BordadoGoku} />
          <Route exact path="/plusultra-tienda/bordado-hisoka" component={BordadoHisoka} />
          <Route exact path="/plusultra-tienda/bordado-hitachi" component={BordadoHitachi} />
          <Route exact path="/plusultra-tienda/bordado-naruto" component={BordadoNaruto} />
          <Route exact path="/plusultra-tienda/bordado-todoroki" component={BordadoTodoroki} />

          <PrivateRoute exact path="/micuenta" component={MiCuenta} />
                   
        </Switch>     
        <Footer/>              
    </div>
    
    </ThemeProvider>
    </Router>
    </AuthProvider>
  );
}

export default App;

